

























import { Component, Prop, Vue } from "vue-property-decorator";

import api from "@/services/api";

@Component
export default class TeacherListItem extends Vue {
  @Prop() private teacher!: {
    id: number;
    avatar: string;
    bio: string;
    cost: number;
    name: string;
    sbject: string;
    whatsapp: string;
  };

  createNewConnection() {
    api.post('connections', {
      user_id: this.teacher.id
    });
  }
}
